/** ******************************************
 **************** 实例 ************************
 *********************************************/

import Layout from '@/layout/index'

export default {
  path: '/example',
  redirect: '/example/index',
  component: Layout,
  children: [
    {
      path: 'index',
      name: 'Example',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/index.vue'),
      meta: {
        title: '实例'
      }
    }, {
      path: 'lazyload',
      name: 'LazyLoad',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/lazyload/index.vue'),
      meta: {
        title: '懒加载'
      }
    }, {
      path: 'carousel',
      name: 'Carousel',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/carousel/index.vue'),
      meta: {
        title: '走马灯'
      }
    }, {
      path: 'datepicker',
      name: 'DatePicker',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/datepicker/index.vue'),
      meta: {
        title: '日期'
      }
    }, {
      path: 'select',
      name: 'Select',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/select/index.vue'),
      meta: {
        title: '下拉选择'
      }
    }, {
      path: 'websocket',
      name: 'WebSocket',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/websocket/index.vue'),
      meta: {
        title: '及时通信'
      }
    }, {
      path: 'radio',
      name: 'Radio',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/radio/index.vue'),
      meta: {
        title: 'Radio'
      }
    }, {
      path: 'table',
      name: 'Table',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/table/index.vue'),
      meta: {
        title: 'Table'
      }
    }, {
      path: 'cascader',
      name: 'cascader',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/cascader/index.vue'),
      meta: {
        title: 'Cascader'
      }
    }, {
      path: 'captcha',
      name: 'Captcha',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/captcha/index.vue'),
      meta: {
        title: 'Captcha'
      }
    }, {
      path: 'scrollnumber',
      name: 'ScrollNumber',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/scrollnumber/index.vue'),
      meta: {
        title: 'ScrollNumber'
      }
    }, {
      path: 'directive',
      name: 'Directive',
      component: () => import(/* webpackChunkName: "example" */ '@/views/example/directive/index.vue'),
      meta: {
        title: 'Directive'
      }
    }
  ]
}
