<template>
  <div class="nav-bar">
    <router-link v-for="item in nav" :key="item.id" class="nav-link" :to="item.link">{{ item.name }}</router-link>
    <button class="logout" @click="logout">退出</button>
  </div>
</template>

<script>
export default {
  name: 'NavBar',
  data() {
    return {
      nav: [
        { id: 1, name: '首页', link: '/dashboard' },
        { id: 2, name: '实例', link: '/example' },
        { id: 3, name: '组件', link: '/components' },
        { id: 4, name: '关于', link: '/about' }
      ]
    }
  },
  methods: {
    logout() {
      this.$store.dispatch('user/logout').then(() => {
        this.$router.push('/login')
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.nav-bar {
  height: 48px;
  vertical-align: middle;
  background-color: #404040;
  .nav-link {
    display: inline-block;
    padding: 0 16px;
    line-height: 48px;
    color: #fff;
    &.router-link-active {
      background-color: #000;
    }
  }
  .logout {
    float: right;
    margin-top: 10px;
    margin-right: 20px;
    border-width: 0;
    color: #FFF;
    background-color: transparent;
    cursor: pointer;
  }
}
</style>
