<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <Carousel
      :loopsList="setting.loopsList"
      :loopsIndex="setting.loopsIndex"
      :autoplaySpeed="setting.autoplaySpeed"
      :autoPlay="setting.autoPlay"
      :radiusDot="setting.radiusDot"
      class="loops-height"
    ></Carousel>
    <button class="add-button">添加到桌面</button>
    <img src="images/banner-01.jpg" class="banner-image" alt="a fox picture">
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import Carousel from '@/components/Carousel/Carousel.vue'

export default {
  name: 'home',
  components: {
    // HelloWorld,
    Carousel
  },
  data() {
    return {
       setting: {
        loopsList: [
          { id: 1, url: '', img: require('../assets/images/153828952.jpg') },
          { id: 2, url: '', img: require('../assets/images/25cbcabf0e8083869e693db485552710.jpg') },
          { id: 3, url: '', img: require('../assets/images/c4bd07c5f2e537a97202dc347d025c5c.jpg') }
        ],
        loopsIndex: 0,
        autoplaySpeed: 3000,
        autoPlay: true,
        radiusDot: true
      }
    }
  },
  mounted() {
    const images = ['banner-01', 'banner-02', 'banner-03', 'banner-01'];
    const imgElem = document.querySelector('img');

    function randomValueFromArray(array) {
      const randomNo = Math.floor(Math.random() * array.length);
      return array[randomNo];
    }

    setInterval(() => {
      const randomChoice = randomValueFromArray(images);
      imgElem.src = `images/${randomChoice}.jpg`;
    }, 2000);

    // Register service worker to control making site work offline

    if ('serviceWorker' in navigator) {
      navigator.serviceWorker
        .register('/sw.js')
        .then(() => { console.log('Service Worker Registered'); });
    }

    // Code to handle install prompt on desktop

    let deferredPrompt;
    const addBtn = document.querySelector('.add-button');
    addBtn.style.display = 'none';

    window.addEventListener('beforeinstallprompt', (e) => {
      // Prevent Chrome 67 and earlier from automatically showing the prompt
      e.preventDefault();
      // Stash the event so it can be triggered later.
      deferredPrompt = e;
      // Update UI to notify the user they can add to home screen
      addBtn.style.display = 'block';

      addBtn.addEventListener('click', () => {
        // hide our user interface that shows our A2HS button
        addBtn.style.display = 'none';
        // Show the prompt
        deferredPrompt.prompt();
        // Wait for the user to respond to the prompt
        deferredPrompt.userChoice.then((choiceResult) => {
          if (choiceResult.outcome === 'accepted') {
            console.log('User accepted the A2HS prompt');
          } else {
            console.log('User dismissed the A2HS prompt');
          }
          deferredPrompt = null;
        });
      });
    });
  }
}
</script>

<style lang="scss" scoped>
.banner-image {
  width: 100%;
}
.add-button {
  position: absolute;
  top: 1px;
  left: 1px;
}
</style>
